//统计接口1
    shujufenxiChat1() {
      this.$nextTick(()=>{

        var shujufenxiChart1 = echarts.init(document.getElementById("shujufenxiChart1"),'macarons');
        this.$http({
            url: `shujufenxi/value/bianhao/xueya`,
            method: "get",
        }).then(({ data }) => {
            if (data && data.code === 0) {
                let res = data.data;
                let xAxis = [];
                let yAxis = [];
                let pArray = []
                for(let i=0;i<res.length;i++){
                    xAxis.push(res[i].bianhao);
                    yAxis.push(parseFloat((res[i].total)));
                    pArray.push({
                        value: parseFloat((res[i].total)),
                        name: res[i].bianhao
                    })
                }
                var option = {};
				let titleObj = this.line.title
				titleObj.text = '血压统计'
				
				const legendObj = this.line.legend
				
				let xAxisObj = this.line.xAxis
				xAxisObj.type = 'category'
				xAxisObj.boundaryGap = false
				xAxisObj.data = xAxis
                xAxisObj.axisLabel.rotate=70
				
				let yAxisObj = this.line.yAxis
				yAxisObj.type = 'value'
				
				let seriesObj = {
					data: yAxis,
					type: 'line',
				}
				seriesObj = Object.assign(seriesObj , this.line.series)
				
                option = {
					backgroundColor: this.line.backgroundColor,
					color: this.line.color,
                    title: titleObj,
					legend: legendObj,
                    tooltip: {
						trigger: 'item',
						formatter: '{b} : {c}'
                    },
                    xAxis: xAxisObj,
                    yAxis: yAxisObj,
                    series: [seriesObj]
                };
                // 使用刚指定的配置项和数据显示图表。
                shujufenxiChart1.setOption(option);
				

                  //根据窗口的大小变动图表
                window.onresize = function() {
                    shujufenxiChart1.resize();
                };
            }
        });
      })
    },

